.slick-prev:before,
.slick-next:before {
  color: gray !important;
  font-size: 3rem !important;
}

.width-item {
  width: 100% !important;
}
.slick-track {
  width: 100% !important;
}
.active_Film {
  @apply text-white bg-gray-800;
}

.none_active_Film {
  @apply text-gray-800 bg-white;
}
